@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500&family=Montserrat:wght@700&family=Roboto:wght@400;700&display=swap);
.header{height:10vh;background-color:#f0f0f0;display:flex;align-items:center;justify-content:space-between;padding:0 15px}.logo{display:block;color:#000;text-decoration:none;font-size:32px;line-height:1.2;text-transform:uppercase;font-family:"Fira Sans";letter-spacing:1px}.nav__link{font-size:18px;line-height:1.2;text-transform:uppercase;text-decoration:none;font-family:"Fira Sans", sans-serif;color:#000;margin-left:30px}

.page__inner{display:grid;grid-template-columns:1fr 1fr;grid-gap:30px;margin:30px 15px;padding:30px;background-color:#fff;border-radius:10px}.form__header{display:flex;align-items:center;height:40px;margin-bottom:22px}.form__header-title{font-size:20px;line-height:36px;font-weight:700;font-family:Roboto, sans-serif}.form__header .btn-edit{margin-left:14px}.form__body{display:grid;grid-template-columns:1fr 1fr;grid-gap:10px}.form__item{font-family:"Fira Sans", sans-serif;line-height:18px;display:flex;flex-direction:column;margin-bottom:35px}.form__item-required{color:#ff5500}.form__item-label{font-size:14px;margin-bottom:14px;position:relative}.form__item-value{font-size:16px;font-weight:500;text-transform:capitalize}.form__item-input{padding:13px;border-radius:4px;border:1px solid #e8e8e8;text-transform:capitalize}.form__btn{font-family:Montserrat, sans-serif;width:160px;height:50px;text-transform:uppercase;background-color:#6bd098;border:none}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}body{background-color:#e7e6e6}

